            <div class="subtitle">必要性</div>
                <p class="text">节约粮食，是我们每个公民应尽的义务，而不是说你的生活好了，你浪费得起就可以浪费。浪费是一种可耻的行为。只要存有节约的意识，其实做起来很简单：吃饭时吃多少盛多少，不扔饭菜；在餐馆用餐时点菜要适量，而不应该摆阔气，乱点一气。记住:节约粮食从我做起。</p>
                <p class="text">浪费可耻，从某种意义上说简直是“犯罪”。凡事贵在坚持，节约要从点滴做起，尤其需要坚持。一天两天可以，一年两年呢？更长的时间呢？不仅仅是对粮食，要让节约内化为自觉，成为习惯。而且，在长征途中，有许多革命先烈因饥寒交迫而死。所以，我们要节约粮食，要不然我们怎么对得起那些革命先烈呢！</p>
            <div class="inner-border"></div>

            <div class="subtitle">重要指示</div>
                <p class="text">2020年8月11日，中共中央总书记、国家主席、中央军委主席习近平对制止餐饮浪费行为作出重要指示。他指出，餐饮浪费现象，触目惊心、令人痛心！“谁知盘中餐，粒粒皆辛苦。”尽管我国粮食生产连年丰收，对粮食安全还是始终要有危机意识，2020年全球新冠肺炎疫情所带来的影响更是给我们敲响了警钟。</p>
                <p class="text">习近平强调，要加强立法，强化监管，采取有效措施，建立长效机制，坚决制止餐饮浪费行为。要进一步加强宣传教育，切实培养节约习惯，在全社会营造浪费可耻、节约为荣的氛围。</p>
                <p class="text">习近平一直高度重视粮食安全和提倡“厉行节约、反对浪费”的社会风尚，多次强调要制止餐饮浪费行为。2013年1月，习近平就作出重要指示，要求厉行节约、反对浪费。此后，习近平又多次作出重要指示，要求以刚性的制度约束、严格的制度执行、强有力的监督检查、严厉的惩戒机制，切实遏制公款消费中的各种违规违纪违法现象，并针对部分学校存在食物浪费和学生节俭意识缺乏的问题，对切实加强引导和管理，培养学生勤俭节约良好美德等提出明确要求。</p>
            <div class="inner-border"></div>

            <div class="subtitle">建议</div>
                <p class="text">1、珍惜粮食,适量定餐,避免剩餐,减少浪费。</p>
                <p class="text">2、不攀比,以节约为荣,浪费为耻。</p>
                <p class="text">3、吃饭时吃多少盛多少,不浪费剩饭剩菜。</p>
                <p class="text">4、看到浪费现象勇敢地起来制止,尽力减少浪费。</p>
                <p class="text">5、做节约宣传员,向家人,亲戚,朋友宣传浪费的可怕后果。</p>
                <p class="text">6、不偏食，不挑食。</p>
                <p class="text">7、到饭店吃饭时，点饭点菜不浪费，若有剩余的要尽量带回家。</p>
